.dialogContainer {
  pointer-events: none; }

.dialog {
  pointer-events: all;
  color: #303030;
  border-radius: 6px;
  max-width: 350px;
  box-shadow: 0 1px 5px 0 rgba(0, 0, 0, 0.2), 0 0 0px 1px rgba(0, 0, 0, 0.05);
  cursor: default;
  opacity: 0.98;
  font-size: 17px;
  line-height: 1.3;
  background: #fff;
  margin: 20px;
  overflow: hidden;
  text-align: left;
  transform: translate3d(0, 0, 0);
  transition: all 0.75s, height 0s;
  transition-timing-function: cubic-bezier(0.25, 1.37, 0.44, 0.93);
  -webkit-tap-highlight-color: transparent; }

.dialog:hover {
  opacity: 1;
  box-shadow: 0 4px 10px 0 rgba(0, 0, 0, 0.2), 0 0 0px 1px rgba(0, 0, 0, 0.05); }

.dialogContainer,
.dialog {
  position: fixed;
  top: 0;
  right: 0;
  z-index: 300; }

.dialog .button.small {
  margin-left: 4px;
  margin-right: 4px; }

.dialog .button {
  margin-left: 7px;
  margin-right: 7px; }

.dialogContainer:not(.bottom) .dialog + .dialog {
  margin-bottom: 0px; }

.dialogContainer.bottom .dialog + .dialog {
  margin-top: 0px; }

.dialogContainer.bottom,
.dialog.bottom {
  top: inherit;
  bottom: 0; }

.dialogContainer.left,
.dialog.left {
  right: inherit;
  left: 0; }

.dialogContainer > .dialog {
  position: relative; }

.dialog.hidden {
  display: none; }

/* 
.dialogContainer > .dialog:nth-child(1){ -webkit-transition-delay: 0.1s; transition-delay: 0.1s; }
.dialogContainer > .dialog:nth-child(2){ -webkit-transition-delay: 0.2s; transition-delay: 0.2s; }
.dialogContainer > .dialog:nth-child(3){ -webkit-transition-delay: 0.3s; transition-delay: 0.3s; }
.dialogContainer > .dialog:nth-child(4){ -webkit-transition-delay: 0.4s; transition-delay: 0.4s; }
.dialogContainer > .dialog:nth-child(5){ -webkit-transition-delay: 0.5s; transition-delay: 0.5s; }
*/
.dialog.hide {
  transform: scale(0.5);
  opacity: 0 !important;
  transition: height 0s, opacity 0.5s, transform 0.5s !important; }

.dialog.reveal {
  -webkit-transition: 0s !important;
  transition: 0s !important; }

@-webkit-keyframes revealDialog {
  from {
    opacity: 0;
    margin-top: 0;
    transform: scale(0.5) translateY(100%); } }

@keyframes revealDialog {
  from {
    height: 0;
    opacity: 0;
    margin-top: 0;
    transform: scale(0.5) translateY(100%); } }

.dialog:hover {
  opacity: 1;
  transition: 0.35s; }

html:not(.page-loaded) .dialog {
  transform: scale(0.5) translateY(100%);
  opacity: 0; }

.dialog .close {
  padding: 15px;
  position: absolute;
  font-size: 0;
  top: 0;
  right: 0;
  cursor: pointer;
  transition: 0.35s;
  opacity: 0;
  z-index: 10; }

.dialog:hover .close {
  opacity: 0.2; }

.dialog .close:hover, .mobile .dialog .close {
  opacity: 0.5; }

.dialog .close:active {
  opacity: 1; }

.dialog .close:after,
.dialog .close:before {
  content: "";
  width: 14px;
  height: 2px;
  border-radius: 2px;
  background: #202020;
  transform: rotate(45deg);
  position: absolute;
  right: 8px; }

.dialog .close:before {
  transform: rotate(-45deg); }

.dialog .close.white {
